<template>
  <el-row :gutter="10">
    <el-col :span="24" class="m-top-md">
      <div class="grid-content bg-purple-dark">
        <el-card shadow="hover" :body-style="{ overflow: 'hidden' }">
            <div slot="header" class="clearfix">
              <span>{{data.title}}</span>
            </div>
            <el-col :span="8">
                <el-card shadow="hover">
                  <el-input placeholder="请输入内容" style="width:100%" v-model="query" size="medium" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                  <div class="tagItem">
                    <el-tag
                      size="small"
                      style="margin-bottom: 5px;"
                      v-for="item in leftTags"
                      @click.native="addTag(item.code)"
                      :key="item.code">
                      {{item.name}}
                    </el-tag>
                  </div>
                </el-card>
            </el-col>
            <el-col :span="16">
              <div class="grid-content bg-purple-dark">
                  <el-table border size="mini" class="tb-edit" :data="data.split" highlight-current-row>
                    <el-table-column prop="title" label="标签" width="150">
                      <template slot-scope="scope">
                        <el-input
                          size="small"
                          v-if="scope.row.isEdit"
                          style="width: 100%"
                          v-model="scope.row.title"
                          placeholder="请输入内容">
                        </el-input>
                        <span v-else>{{scope.row.title}}</span>
                      </template>
                    </el-table-column>
                    <el-table-column prop="tags" label="标签范围">
                      <template slot-scope="scope">
                        <el-tag
                          disable-transitions size="small"
                          :closable="scope.row.isEdit"
                          v-for="tag in getTags(scope.row.tags)"
                          @close="removeTag(tag.code)"
                          :key="tag.code">
                          {{tag.name}}
                        </el-tag>
                      </template>
                    </el-table-column>
                    <el-table-column
                      fixed="right"
                      label="操作"
                      width="100">
                      <template slot-scope="scope">
                          <el-tooltip class="item" effect="dark" content="编辑" placement="top">
                              <el-button
                              size="small"
                              type="text"
                              @click="edit(scope.row.id)"
                              icon="iconfont icon-bianji"
                              v-show=" currentEdit!== scope.row.id">
                            </el-button>
                          </el-tooltip>
                          <el-tooltip class="item" effect="dark" content="保存" placement="top">
                              <el-button
                              size="small"
                              type="text"
                              @click="save"
                              style="margin-left: 0;"
                              icon="iconfont icon-fuzhi"
                              v-show="currentEdit === scope.row.id">
                            </el-button>
                          </el-tooltip>
                          <el-tooltip class="item" effect="dark" content="删除" placement="top">
                              <el-button
                              @click.native.prevent="removeRecord(scope.row.id)"
                              type="text"
                              size="small"
                              icon="iconfont icon-iconfont-shanchu"
                              style="color: #FE2B6F">
                            </el-button>
                          </el-tooltip>
                      </template>
                    </el-table-column>
                  </el-table>
                  <el-button
                    :style="specialTableBtn"
                    type="primary"
                    icon="el-icon-plus"
                    @click="addRecord">
                    新增行
                  </el-button>
              </div>
            </el-col>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import {generateUUID} from '@/utils/uuid'
export default {
  name: 'TagDimension',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      currentEdit: '',
      query: '',
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      }
    }
  },
  computed: {
    tags () {
      let hasTags = []
      this.data.split.forEach(item => {
        hasTags = hasTags.concat(item.tags)
      })
      return this.data.tags.filter(item => {
        return !hasTags.includes(item.code)
      })
    },
    leftTags () {
      return this.tags.filter(item => {
        return item.name.toLowerCase().indexOf(this.query.toLowerCase()) > -1
      })
    },
    currentEditIndex () {
      return this.data.split.findIndex(item => item.id === this.currentEdit)
    }
  },
  methods: {
    getTags (codes) {
      return codes.map(item => {
        return this.data.tags.find(tag => tag.code === item)
      })
    },
    addTag (code) {
      if (this.currentEditIndex > -1) {
        this.data.split[this.currentEditIndex].tags.push(code)
      }
    },
    removeTag (code) {
      const index = this.data.split[this.currentEditIndex].tags.findIndex(item => item === code)
      this.data.split[this.currentEditIndex].tags.splice(index, 1)
    },
    addRecord () {
      const id = generateUUID()
      const newItem = {id: id, isEdit: true, title: '', tags: []}
      this.data.split.push(newItem)
      this.edit(id)
    },
    removeRecord (id) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.data.split.findIndex(item => item.id === id)
        if (index === this.currentEditIndex) {
          this.currentEdit = ''
        }
        this.data.split.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    },
    edit (id) {
      if (this.currentEditIndex > -1) {
        this.data.split[this.currentEditIndex].isEdit = false
      }
      this.currentEdit = id
      this.data.split[this.currentEditIndex].isEdit = true
    },
    save () {
      this.data.split[this.currentEditIndex].isEdit = false
      this.currentEdit = ''
    }
  }
}
</script>

<style scoped>
  .el-tag--small{
    margin: 2px 5px 2px 0!important;
  }
  .el-card{
    margin-bottom: 10px;
  }
  div >>> .el-card__header{
    background-color: #15afff;
    color: #ffffff
  }
  .special-table-btn{
    width: 100%;
    margin-top: 5px;
    border: 1px dashed #15afff;
    background: #fff;
    color: #15afff;
    display: block;
  }
</style>



